// ajax.js
const BASE_URL = 'http://localhost:3000';
const processEnv = process.env || 'development';
import { dataBlocks, songInfo, lyric, playlist } from "./api.js";

export default function Ajax({ //è¯·æ±‚å‚æ•°é…ç½®
    method = "GET",  //é»˜è®¤ä¸º'get'è¯·æ±‚
    url,
    data = {}
}) {
    return new Promise(resolve => { // è¿”å›å¼‚æ­¥è¯·æ±‚
        const xhr = new XMLHttpRequest();
        xhr.open(method, BASE_URL + url)
        xhr.onload = function () {
            resolve(JSON.parse(xhr.response))
        }
        xhr.onerror = function () {
            console.log(xhr)
            if (xhr.status == 0) {

            }
        }
        xhr.send(JSON.stringify(data));
    })
}

/**
 * @description: è·å¾—è½®æ’­å›¾ä¿¡æ¯
 * @param {*}
 * @return {*}
 */
export async function getBannerList() {
    let result;
    if (processEnv === 'demo') {
        result = dataBlocks;
    } else {
        result = Ajax({
            url: `/homepage/block/page`
        })
    }

    return result;
}

/**
 * @description: è·å¾—æ¨èæ­Œå•åˆ—è¡¨
 * @param {*} musicId
 * @return {*}
 */
export async function getRecommendList(musicId) {
    let result;
    if (processEnv === 'demo') {
        result = playlist;
    } else {
        result = Ajax({
            url: `/playlist/detail?id=${musicId}`
        })
    }
    // const result = Ajax({
    //     url: `/playlist/detail?id=${musicId}`
    // })
    return result;
}

